.formulario {
    display: flex;
    justify-content: space-between;
    margin: 80px 50px;
    flex-wrap: wrap;
    gap: 20px;
}

.formulario form {
    background-color: #F6F6F6;
    box-shadow: 7px 7px 15px rgba(0, 0, 0, 25%);
    border-radius: 20px;
    padding: 8px 100px;
    flex: 1;
}

.formulario h2 {
    font-size: 32px;
    font-weight: 400;
    color: #212121;
}