button {
    background-color: #6278f7;
    border-radius: 10px;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
    padding: 24px 32px;
    border: none;
    color: #fff;
    margin: 32px 0;
    cursor: pointer;
    transition: color ease-in-out 0.2s;
}

button:hover {
    color: #95FFD4;
}